.local_loader {
  position: absolute;
  align-self: center;
  justify-self: center;
}

.local_loader_container {
  width: 100%;
  height: 100%;
  background: #ffffffc2;
  position: absolute;
  display: grid;
}

.show_loader_app {
  opacity: 1;
}

.Modal.aparecer {
  -webkit-animation-name: aparece;
          animation-name: aparece;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

@-webkit-keyframes aparece {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes aparece {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Modal-close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -25px;
  top: -25px;
  border-radius: 50%;
  border: none;
  background: #171c30;
  color: white;
  cursor: pointer;
}

.Modal-close:after {
  content: "X";
  font-weight: bold;
  font-size: 30px;
}

