.crop-container {
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  height: 100%;
  position: relative;
  align-self: center;
}

.ImgCropcontrols {
  margin: auto;
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(black));
  background: linear-gradient(to bottom, transparent, black);
  justify-content: center;
}

.slider {
  width: 200px;
  height: 10px;
}

.crop-controls svg {
  cursor: pointer;
}

.crop-controls {
  display: grid;
  background: #ffffff;
  border-radius: 4px;
  grid-template-columns: 1fr auto;
  align-items: center;
  padding: 0 30px;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
  max-width: 700px;
  justify-self: center;
  width: 100%;
}

.crop-controls div:nth-child(1) {
  width: auto;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
}

.crop-controls .crop-rotate-button {
  color: gray;
  background: transparent;
  border: none;
  font-size: 20px;
  margin-top: 6px;
  width: 60px;
  height: 30px;
  cursor: pointer;
  text-align: center;
  line-height: 30px;
}

[data-rmiz-wrap="visible"],
[data-rmiz-wrap="hidden"] {
  position: relative;
  display: inline-flex;
  align-items: flex-start;
}
[data-rmiz-wrap="hidden"] {
  visibility: hidden;
}
[data-rmiz-overlay] {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition-property: background-color;
  transition-property: background-color;
}
[data-rmiz-btn-open],
[data-rmiz-btn-close] {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;

  /* reset styles */
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  font: inherit;
  color: inherit;
  background: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
[data-rmiz-btn-open] {
  cursor: -webkit-zoom-in;
  cursor: zoom-in;
}
[data-rmiz-btn-close] {
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
}
[data-rmiz-modal-content] {
  position: absolute;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transform-origin: center center;
          transform-origin: center center;
}

